<template>
  <div class="row">

    <div class="col-lg-4 col-lg-offset-4 col-md-6 col-md-offset-3 mt100">
      <div class="text-primary f25 text-center mb20">欢迎登录</div>
      <!--输入框开始-->
      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-user w14"></i></span>
        <input type="email" class="form-control" placeholder="邮箱" v-model="user.email"
               @keyup.enter="login">
      </div>
      <div class="input-group mb15">
        <span class="input-group-addon"><i class="fa fa-unlock-alt w14"></i></span>
        <input type="password" class="form-control" placeholder="密码" v-model="user.localPassword"
               @keyup.enter="login">
      </div>

      <div class="row mb15" v-if="loginFail">
        <div class="col-xs-8">

          <div class="input-group">
            <span class="input-group-addon"><i class="fa fa-file-image-o w14"></i></span>
            <input type="text" class="form-control" placeholder="图形验证码" v-model="captchaValue"
                   @keyup.enter="login">
          </div>
        </div>
        <div class="col-xs-4">
          <NbImageCaptcha/>
        </div>
      </div>

      <div class="mb15">
        <button class="btn btn-primary button full-width" @click.prevent.stop="login" :disabled="user.loading">
          <span v-if="user.loading">
            <i class="fa fa-spinner fa-spin"></i>
            正在登录...
          </span>
          <span v-else>
            <i class="fa fa-user-circle-o"></i>
            登录
          </span>

        </button>

      </div>


      <div class="mb15" v-show="user.errorMessage">
        <div class="alert alert-danger">
          {{user.errorMessage}}
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import NbImageCaptcha from "../../common/widget/NbImageCaptcha.vue"
  import {startWith} from "../../common/filter/str";

  export default {
    data() {
      return {
        user: this.$store.state.user,
        captchaValue: null,
        loginFail: false,
        redirect: this.$route.query.redirect
      }
    },
    props: {},
    watch: {},
    computed: {},
    components: {
      NbImageCaptcha
    },
    methods: {
      login() {
        let that = this
        this.user.httpLogin(that.captchaValue, function () {
          that.loginFail = false;


          //自动跳转到之前的页面中去。
          if (that.redirect) {

            if (startWith(that.redirect, "/by")) {
              that.$router.push(that.redirect);
            } else {
              location.href = that.redirect;
            }
          } else {
            //自动进入到首页。
            that.$router.push("/by")
          }


          //登录成功啦。
        }, function (err) {
          that.loginFail = true

          console.log(err)

        })

      },
      logout() {

        if (this.user.role !== "GUEST") {
          this.user.httpLogout(function () {
            console.info("退出成功！")
          }, function () {
            console.error("退出失败！")
          })
        }


      }
    },
    mounted() {
      //到登录页面了需要先做一次退出操作。因为退出就是直接跳转到登录页面的。
      this.logout()
    }


  }

</script>

<style lang="less" rel="stylesheet/less">

</style>





